.drobdown-block {
  position: absolute;
  left: 0;
  top: 100%;
  width: auto;
  max-width: 400px;
  background-color: #fff;
  border-radius: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-content: stretch;
  align-content: stretch;
  border-top: 3px solid #40B6E9;
  border-radius: 19px;
  padding: 20px 25px;
  margin-top: 10px;
}

.drobdown-block {
  height: 0;
  opacity: 0;
  overflow: hidden;
  z-index: -1;
  transition: all 0.2s linear;
}

.openes .drobdown-block {
  height: auto;
  opacity: 1;
  overflow: visible;
  z-index: 2;
}

.drobdown-list li {
  display: block;
  margin: 10px 0;
}

.drobdown-list li a {
  font-size: 16px;
  min-width: 200px;
}

.drobdown-list li:last-child a {
  font-weight: 600;
  color: #40B6E9;
  margin-top: 20px;
}

